<h1 id="node-js" tabindex="-1"><a class="header-anchor" href="#node-js" aria-hidden="true">#</a> Node.js</h1>
<p>You want to execute your JavaScript code? That you can do in Node.js, a JavaScript runtime environment.</p>
<iframe width="560" height="314" src="https://www.youtube.com/embed/ybIg85flNeA" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
<div class="custom-container warning"><p class="custom-container-title">Node.js support ECMAScript modules</p>
<p>In the lecture slides it says Node.js doesn't support ECMAScript modules. That is no longer the case, see:</p>
<p><a href="https://nodejs.org/api/esm.html" target="_blank" rel="noopener noreferrer">https://nodejs.org/api/esm.html<OutboundLink/></a></p>
<p>However, Require JS and ECMAScript modules aren't 100% interchangeable, so it's probably best to be consistent and use only one of them, and I suspect many npm packages out there don't support ECMAScript modules to 100%, so the safest is probably to stick to using Require JS for now.</p>
</div>
